@import url("https://use.typekit.net/zwv0ssk.css");
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&display=swap');

* {
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 400;
    font-style: normal;
}

h1, h2 {
    font-family: "ivypresto-display", serif;
    font-weight: 300;
    font-style: italic;
}

h2, h3 {
    font-size: 2.5em;
}
h1 {
    font-size: 5em;
}
p {
    font-size: 1.5em;
    line-height: 1.25;
}

.row {
    padding: 0;
    max-width: 100%;
}
.columns {
    padding-left: 0;
    padding-right: 0;
}

.gradient {
    background-image: linear-gradient(#f1f1f1, #000);
    position: relative;
}
.column-align {
    position: relative;
}

.gradient>div>h4 {
    position: absolute;
    bottom: 50px;
}

.gradient>div {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5em;
}
#gradient-3>h3, #gradient-4>h3 {
  color: #fff;
}

#header-1>img, #header-2>img, #header-4>img {
    object-fit: cover;
    height: 100vh;
    max-width: 70vw;
}

#header-3>img {
    object-fit: cover;
    height: 100vh;
    max-width: 50vw;
    width: 50vw;
}

#header-1>h2, #header-2>h2, #header-3>h2, #header-4>h2 {
    padding: 0 2em;
}

#page1 {
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  padding: 0 5em;
  background: #000;
}
#page1>h1 {
  color: #fff;
}

.body>p {
    width: 50vw;
    padding: 40vh 2em;
    text-align: left;
}

#body-end>p {
  padding: 30vh 2em 0;
}

.header {
    min-height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: initial;
}

.page {
  position: relative;
  height: 100%;
  width: 100vw;
  flex-shrink: 0;
}

#page-2 {
    background-color: #000;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

#page-2>div>h2 {
    color: #fff;
    margin-bottom: 2em;
    font-size: 1.75em;
}

#page-2>div:nth-child(1) {
    padding: 0 0 200px 2em;

}

#page-2>div:nth-child(2) {
    padding: 200px 2em 0 0;

}

.container{
    max-width: 650px;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .wrapper{
    border-bottom: 1px solid black;
    overflow: hidden;
  }
  .wrapper .question-container{
    width: 100%;
    text-align: left;
    padding: 20px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    font-size: 20px;
    background: transparent;
    border: none;
    cursor: pointer;
  }
  .question-container.active{
    color: #1db954;
    background-image: linear-gradient(90deg,transparent,rgba(0,0,0,0.04),transparent);
  }
  .wrapper .question-container:hover{
    background-image: linear-gradient(90deg,transparent,rgba(0,0,0,0.04),transparent);
  }
  .wrapper .arrow{
    font-size: 2rem;
    transition: .5s ease-in-out;
  }
  .arrow.active{
    rotate: 180deg;
    color: #1db954;
  }
  .wrapper .answer-container{
    padding: 0 1rem;
    transition: height .7s ease-in-out;
  }
  .wrapper .answer-content{
    padding: 1rem 0;
    font-size: 20px;
    font-style: italic;
  }